$arrow-size: 125px;
$logo-size: 175px;
$arrow-description-margin: 75px;

.home {
    section {
        padding: 1rem;

        &:nth-child(odd) {
            background-color: var(--color-light-grey);
        }
    }

    .banner {
        position: relative;
        color: white;
        height: 75vh;
        background-size: cover !important;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 10% 0;

        background: url("../../assets/images/home/home-banner.jpeg") center 40%;

        filter: contrast(150%) brightness(100%) grayscale(1px);

        .banner__text {
            margin-left: 5vw;
            margin-top: 15vh;
            h1 {
                color: white;
                font-size: 36px;
            }

            h2 {
                color: white;
                font-size: var(--font-size-xxlarge);
            }
        }

        &:before {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            opacity: 0.45;
        }
    }

    .intro-container {
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        position: relative;

        .intro-container__image {
            flex-shrink: 1;
            margin-right: 1rem;
            .logo {
                height: auto;
                width: $logo-size;
            }
        }
        .intro-container__description {
            padding-top: 1rem;
            flex-grow: 1;
            line-height: 1.5;
            max-width: 60%;
        }
    }

    .images-container {
        display: flex;
        .logo-description-container {
            flex: 1;
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: flex-start;
            margin-top: 1rem;

            .logo-description-container__arrow {
                width: $arrow-size;
                height: auto;
                transform: rotate(95deg);
            }

            .logo-description-container__description {
                margin-top: $arrow-description-margin;
                margin-left: 1rem;
            }
        }

        .dreamy-pekkers-description-container {
            flex: 1;
            display: flex;
            position: relative;
            justify-content: flex-end;
            .dreamy-pekkers-description-container__arrow-container {
                position: absolute;
                top: -1rem;
                right: calc(25vw - 2rem);
                .arrow-container__arrow {
                    width: $arrow-size;
                    height: auto;
                    transform: rotate(205deg);
                }
            }

            .dreamy-pekkers-description-container__image {
                display: flex;
                margin-top: $arrow-description-margin;

                .image__description {
                    margin-right: 1rem;
                }

                .image__image {
                    width: 25vw;
                    height: auto;
                }
            }
        }
    }
}
