.whoAreWe {
    .card {
        margin: 1rem;
        display: flex;

        &.collaboration {
            margin: 2rem 15vw 1rem 15vw;
            img {
                width: 25%;
            }

            .card__description {
                width: 75%;
            }
        }

        img {
            width: 15%;
        }
        .card__description {
            width: 85%;
            margin-left: 1rem;

            &:nth-child(odd) {
                margin-left: unset;
                margin-right: 1rem;
            }
        }
    }
}
